import tuiCountdown from "@/components/countdown/countdown"

// 获取秒杀页面的数据
import { secKillData,getSecKillData } from '@/network/Home.js'
import { replaceImage } from '@/utils/dealUrl'
export default {
	components:{
		tuiCountdown
	},
	data() {
		return {
			width:'50%',//进度条的长度
			tabbar:[],
			winHeight: "", //窗口高度
			currentTab: 0, //预设当前项的值
			scrollLeft: 0 ,//tab标题的滚动条位置
			timeSeckillList:[{
				image:'../../../static/demo1.png',
				title:'棉拖鞋秋冬女厚底拖鞋家居款棉拖鞋秋冬女厚底拖鞋家居款',
				width:80,
				sales:1,
				price:21,
				ot_price:12
			},{
				image:'../../../static/demo1.png',
				title:'棉拖鞋秋冬女厚底拖鞋家居款棉拖鞋秋冬女厚底拖鞋家居款',
				width:80,
				sales:1,
				price:21,
				ot_price:12
			},{
				image:'../../../static/demo1.png',
				title:'棉拖鞋秋冬女厚底拖鞋家居款棉拖鞋秋冬女厚底拖鞋家居款',
				width:80,
				sales:1,
				price:21,
				ot_price:12
			}],//秒杀商品列表
			loadingPlan:80,
			skllTime:{},//秒杀时间
			
		}
	},
	onLoad: function() {
		let that = this;
		//  高度自适应
		uni.getSystemInfo({
			success: function(res) {
				let calc = res.windowHeight;
				that.winHeight = calc;
			}
		});
		
	},
	onShow() {		
		this.currentTab = 0
		// 获取秒杀时间列表和商品列表
		this.getSecKillData()
	},
	methods: {
		// 加载秒杀数据
		secKillData(id,pages){
			secKillData(id,pages).then(res => {
				if(res.data.code == 200){
					const num =  res.data.data.backtime.i*60 + res.data.data.backtime.h*3600 + res.data.data.backtime.s *1
					if(num){
						this.skllTime = num
					}else{
						this.skllTime = {}
					}
					const list = res.data.data.seckillInfo
					list.forEach(x => {
						x.image = replaceImage(x.image)
					})
					this.timeSeckillList = list
				}
			})
		},
		
		// 获取秒杀时间列表
		getSecKillData(){
			getSecKillData()
			.then(res => {
				console.log(res)
				if(res.data.code == 200){
					 this.tabbar = res.data.data.seckillTime
					this.tabbar.forEach(x => {
						this.$set(x,'pages',1)
					})
					
					// 守下加载第一页数据
					const id = this.tabbar[0] ? this.tabbar[0].id : ''
					const pages = this.tabbar[0].pages ? this.tabbar[0].pages : ''
					console.log(id,pages)
					// 先加载秒杀第一页数据
					this.secKillData(this.tabbar[0].id,this.tabbar[0].pages)
				}
			})
		},
		
		//秒杀详情
		timeSeckillClick(item){
			const curTab = this.tabbar[this.currentTab]
			if(curTab.state == '抢购中'){
				this.$store.commit('setSkillId',item.id)
				uni.navigateTo({
					url:'../../ShopDetails/seckillTime'
				})
			}
			
		},
		// 滚动切换标签样式
		switchTab: function(e) {
			let that = this;
			that.currentTab = e.detail.current;
			that.checkCor();
		},
		// 点击标题切换当前页时改变样式
		swichNav: function(e) {
			let cur = e.currentTarget.dataset.current;
			if (this.currentTab == cur) {
				return false;
			} else {
				this.currentTab = cur
				this.secKillData(this.tabbar[this.currentTab].id,this.tabbar[this.currentTab].pages)
			}
		},
		//判断当前滚动超过一屏时，设置tab标题滚动条。
		checkCor: function() {
			if (this.currentTab > 3) {
				//这里距离按实际计算
				this.scrollLeft = 300
			} else {
				this.scrollLeft = 0
			}
		},
		detail(e) {
			uni.navigateTo({
				url: '../extend-view/newsDetail/newsDetail'
			})
		}
	},
	computed:{
		typeTime(){
			return this.tabbar[this.currentTab].state
		},
		timeList(){
			return this.skllTime
		}
	}
}
